import React from "react";
import { useTranslation } from "react-i18next";
import { Page } from "common/components";
import AboutUSEn from "./about-us-en";
import AboutUSAr from "./about-us-ar";
import MetaTags from "common/MetaTags";
import { descriptions, getDescription } from "../descriptions";

const AboutUs = ({ contentId, titleId }) => {
  const { t, i18n } = useTranslation();

  const getTitle = () => t(titleId);

  const metaTags = [
    {
      name: "description",
      content: getDescription(contentId, i18n.language),
    },
  ];

  return (
    <Page title={getTitle()}>
      <MetaTags title={getTitle()} tags={metaTags} />
      <div className="content">
        <div className="container">
          <div className="row">
            <div className="col-12">
              {i18n.language === "ar" ? (
                <AboutUSAr title={getTitle()} />
              ) : (
                <AboutUSEn title={getTitle()} />
              )}
            </div>
          </div>
        </div>
      </div>
    </Page>
  );
};

export default AboutUs;
